/* flex-area */
.flex {
    display: flex;
}

.flex-row{
    flex-direction: row;
}

.flex-column {
    flex-direction: column;
}

.justify-center {
    justify-content: center;
}

.justify-between {
    justify-content: space-between;
} 

.justify-around {
    justify-content: space-around;
}

.align-center {
    align-items: center;
}

.align-start {
    align-items: flex-start;
}

.align-end {
    align-items: flex-end;
}

/* margin & padding Area  */

/* Margin 관련 */
.m-0 {
    margin: 0;
  }
  
  .m-1 {
    margin: 5px;
  }
  
  .m-2 {
    margin: 10px;
  }
  
  .m-auto {
    margin: auto;
  }
  
  /* Padding 관련 */
  .p-0 {
    padding: 0;
  }
  
  .p-1 {
    padding: 5px;
  }
  
  .p-2 {
    padding: 10px;
  }

  /* Text 관련 */
.text-left {
    text-align: left;
  }
  
  .text-center {
    text-align: center;
  }
  
  .text-right {
    text-align: right;
  }
  
  .text-bold {
    font-weight: bold;
  }
  
  .text-normal {
    font-weight: normal;
  }
  
  /* Width 관련 */
  .w-full {
    width: 100%;
  }
  
  .w-half {
    width: 50%;
  }
  
  .w-auto {
    width: auto;
  }
  
  /* Height 관련 */
  .h-full {
    height: 100%;
  }
  
  .h-auto {
    height: auto;
  }

  /* Position 관련 */
  .relative {
    position: relative;
  }
  
  .absolute {
    position: absolute;
  }
  
  .fixed {
    position: fixed;
  }
  
  .top-0 {
    top: 0;
  }
  
  .left-0 {
    left: 0;
  }
  
  